// main div of products
.products-main {
  width: 90%;
  margin: 0 auto;
}

.products-header {
  text-align: center;
  font-size: 26px;
}

.products-wrapper {
  width: 100%;
  max-width: 870px;
  margin: 0 auto;
}
// each individual item
.item-unit {
  width: 210px;
  padding: 0.5em 0.1em;
  margin: 0.7em 0.35em;
  transition: all 1s ease-in-out;

  &:hover {
    border-radius: 12px;
    background-color: rgb(209, 208, 124);
    transition: all 1s ease-in-out;
  }
}

.item-detail {
  margin: 0.1em 0.4em;
  text-align: center;
}

.item-image {
  height: 16.2em;
  background-image: url('../../data/img/dataItems/products/image-sprite.jpg');
}
// loop through sprite image
@for $i from 1 through 8 {
  .item-unit:nth-child(#{$i}) .item-image {
    background-position-y: 1 + -$i * 266px;
  }
}

.buy-it {
  display: block;
  margin: 0 auto;
  border-radius: 38%;
  background-color: rgb(152, 219, 182);
  padding: 0.1em 1em;
  font-weight: 600;
  font-size: 1em;
  cursor: pointer;
  &:hover {
    width: auto;
    padding: 0.9em 1.2em;
    border-radius: 15px;
    border: 2px solid green;
    background-color: rgb(51, 109, 65);
    transition: all .5s ease-in-out;
  }
}

.buy-it {
  display: block;
  padding: 0.5em 1em;
  &:hover {
    padding: 0.4em 1em;
    color: #ccc;
    border-radius: 20px;
  }
}

.carousel-inner {
  height: 40vh;
  position: relative;
  width: 100%;
  overflow: hidden;
  border-radius: 15px;
}

.carousel-control {
  border-radius: 15px;
}
