  .products-header {
    text-align: center;
    font-size: 2.2em;
  }
  .products-wrapper {
    width: 100%;
    margin: 0 auto;
  }
  .item-unit{
    display: inline-grid;
    height: 26em;
    text-align: center;
    padding: 0.5em 0.5em;
    margin: 1em auto;
  }
  .item-detail {
